//   px  rem    1920px
@function s($px) {
  @return calc($px / 10) + rem;
}
//   px  rem    768px
@function s-md($px) {
  @return calc(($px / 10) * 2) + rem;
}

//                                                             mixins
@import 'mixin';
//                                                              
@import 'static/variables';
//                                                             
@import 'static/fonts';

//  
$font-family: 'EuropeExt';
$fonts-1: 'regular' 400, 'bold' 700;
$fonts-2: 'light' 300, 'regular' 400, 'semibold' 600, 'bold' 700;

@each $name, $weight in $fonts-1 {
  @include font($font-family + '-' + $name, $font-family, $weight, 'normal', 'otf', 'opentype');
}
@each $name, $weight in $fonts-2 {
  @include font('GraphikLCG-' + $name, 'GraphikLCG', $weight);
}

html,
body {
  margin: 0 auto;
  padding: 0;
  font-family: $font-family, sans-serif; //   
  font-weight: 700; //    
  font-style: normal;
  line-height: 155%;
}

html {
  font-size: 0.438335vw; //    1920px (0.5208335vw === 10px)
  @include mobile {
    font-size: 5px;
    font-size: 1.5625vw;
    font-size: calc(100 / $breakpoint-mobile) * 5vw; //  $breakpoint-mobile -      px
    -webkit-text-size-adjust: none;
  }
}

body {
  font-size: s(16); //     (16px)
  color: var(--white); //    
  background-color: var(--grey-shadow); //   
  @include mobile {
    font-size: s-md(14); //     (16px)
    line-height: 140%;
    -webkit-text-size-adjust: none;
  }
}

//                                                               
@import 'static/reset';
//                                                                
@import 'static/custom-element';
//                                                               Yandex 
@import 'static/yandex-map';

.container {
  width: s(1683);
  margin: 0 auto;
  @include mobile {
    width: 100%;
    padding: 0 s-md(16);
  }
}

.desktop {
  @include mobile {
    display: none;
  }
}

.mobile {
  display: none;
  @include mobile {
    display: initial;
  }
}

@import './header';
@import './footer';
